#vue {
    background: lightgreen;
}

#vue .logo {
    background: url("../images/vuejs.svg") no-repeat center;
    background-size: contain;
}

@media screen and (min-width:640px) {

    #vue {
        grid-template: repeat(14, 1fr)/repeat(12, 1fr);
        display: grid;
    }

    #vue .cell-1 {
        grid-column: 2/7;
        grid-row: 3/12;
    }

    #vue .cell-2 {
        grid-column: 8/12;
        grid-row: 2/4;
    }

    #vue .cell-3 {
        grid-column: 8/12;
        grid-row: 4/6;
    }

    #vue .cell-4 {
        grid-column: 8/12;
        grid-row: 6/8;
    }

    #vue .cell-5 {
        grid-column: 8/12;
        grid-row: 8/10;
    }

    #vue .cell-6 {
        grid-column: 8/12;
        grid-row: 10/12;
    }

    #vue .cell-7 {
        grid-column: 8/12;
        grid-row: 12/14;
    }
}