#react {
    background-color: skyblue;
}

#react .logo {
    background: url("../images/react-logo.svg") no-repeat center;
    background-size: contain;
}


@media screen and (min-width:640px) {

    #react {
        grid-template: repeat(14, 1fr)/repeat(12, 1fr);
        display: grid;
    }

    #react .cell-1 {
        grid-column: 4/10;
        grid-row: 2/7;
    }

    #react .cell-2 {
        grid-column: 2/4;
        grid-row: 3/5;
    }

    #react .cell-3 {
        grid-column: 10/13;
        grid-row: 3/5;
    }

    #react .cell-4 {
        grid-column: 2/4;
        grid-row: 9/14;
    }

    #react .cell-5 {
        grid-column: 6/8;
        grid-row: 9/14;
    }

    #react .cell-6 {
        grid-column: 10/13;
        grid-row: 9/14;
    }

}