#node {
    background-color: rgba(243, 243, 243, 0.746);
}

#node .logo {
    background: url("../images/nodejs.svg") no-repeat center;
    background-size: contain;
}


@media screen and (min-width:640px) {

    #node {
        grid-template: repeat(14, 1fr)/repeat(12, 1fr);
        display: grid;
    }

    #node .cell-1 {
        grid-column: 4/10;
        grid-row: 2/7;
    }

    #node .cell-2 {
        grid-column: 2/4;
        grid-row: 9/12;
    }

    #node .cell-3 {
        grid-column: 5/7;
        grid-row: 9/12;
    }

    #node .cell-4 {
        grid-column: 8/10;
        grid-row: 9/12;
    }

    #node .cell-5 {
        grid-column: 11/13;
        grid-row: 9/12;
    }


}