#java {
    background: white;
}

#java .logo {
    background: url("../images/java-logo.svg") no-repeat center;
    background-size: contain;
}


@media screen and (min-width:640px) {

    #java {
        grid-template: repeat(14, 1fr)/repeat(12, 1fr);
        display: grid;
    }

    #java .cell-1 {
        grid-column: 4/10;
        grid-row: 2/8;
    }

    #java .cell-2 {
        grid-column: 2/4;
        grid-row: 9/12;
    }

    #java .cell-3 {
        grid-column: 5/7;
        grid-row: 9/12;
    }

    #java .cell-4 {
        grid-column: 8/10;
        grid-row: 9/12;
    }

    #java .cell-5 {
        grid-column: 11/13;
        grid-row: 9/12;
    }


}