.desktop-menu {
    display: none;
}

.desktop-menu {
    position: fixed;
    justify-content: center;
    list-style: none;
    width: 100%;
    height: 10%;
    align-items: center;
    top: 0;
    left: 0;
}

.desktop-menu li {
    margin: 0 10px;
    padding: 6px;
}

.desktop-menu li:hover {
    background-color: lightblue;
}

.mobile-menu {
    display: none;
}

.mobile-menu {
    background-color: $mobile-menu-background;
    height: $menu-height;
    position: fixed;
    width: 100%;
    z-index: 100;

    .hamburger-button {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        width: 3rem;
        margin: 4px;
        height: calc(#{$menu-height} - 8px );
        right: 0;
        background-color: $hamberger-button-background;

        .line {
            height: 4px;
            margin: 6% 0;
            width: 50%;
            background-color: $hamberger-line-background;
            border-radius: 10%;
        }
    }

    .menu-list {
        background-color: $mobile-menu-list-background;
        position: absolute;
        width: 100%;
        left: 100%;
        top: $menu-height;
        transition: left .2s;
        list-style: none;

        li {
            background-color: $mobile-menu-list-li-background;
            text-align: end;
            padding: .4rem;
            margin: 2px 0;
            color: white;
            font-size: 1.5rem;
        }
    }

}


@media screen and (min-width:640px) {
    .desktop-menu {
        display: flex;
    }
}

@media screen and (max-width:639px) {
    .mobile-menu {
        display: block;
    }
}