.home {
    background: honeydew;
    overflow: hidden;
}

@media screen and (max-width:639px) {
    .home {
        display: flex;
        flex-direction: column;
        justify-content: start;

        .home__my-photo {
            align-self: center;
            text-align: center;
            margin: 1rem 0;

            .home__pic {
                min-width: 150px;
                min-height: 150px;
            }
        }

        .home__about {
            padding: 0 1rem;

            .home__list {
                padding-left: 3rem;
            }
        }

    }

}

@media screen and (min-width:640px) {

    .home {
        display: flex;
        align-items: center;
        justify-content: space-around;
        justify-items: center;

        .home__my-photo {
            max-width: 40%;
            flex: 1;
            text-align: center;

            .home__pic {
                border: 1px solid black;
                min-width: 300px;
                min-height: 400px;
            }

            .home__name {
                font-size: 1.6rem;
                margin-top: 1rem;
            }

        }

        .home__about {
            flex-wrap: wrap;
            max-width: 40%;
            flex: 1;
        }

        .home__list {
            padding-left: 3rem;
            margin-top: 1rem;
        }

        .home__list li {
            margin: .3rem 0;
        }
    }

}