#angular {
    background-color: red;
}

#angular .logo {
    background: url("../images/angular-icon.svg") no-repeat center;
    background-size: contain;
}


@media screen and (min-width:640px) {

    #angular {
        grid-template: repeat(14, 1fr)/repeat(12, 1fr);
        display: grid;
    }

    #angular .cell-1 {
        grid-column: 4/10;
        grid-row: 2/7;
    }

    #angular .cell-2 {
        grid-column: 2/5;
        grid-row: 9/14;
    }

    #angular .cell-3 {
        grid-column: 6/9;
        grid-row: 9/14;
    }

    #angular .cell-4 {
        grid-column: 10/13;
        grid-row: 9/14;
    }

}