@import './constants';
@import './RESET';
@import './menu';
@import './home';
@import './vue';
@import './angular';
@import './java';
@import './react';
@import './node';

.container {
    height: 100vh;
    width: 100vw;
}

section {
    width: 100%;
    height: 100%;
    padding-top: $menu-height;
}

.italic{
    font-style: italic;
}

.icon {
    display: inline-block;
    background-size: contain;
    margin-top: 10px;
}

.icon-skype {
    background-image: url(../images/icons/skype.svg);
}

.icon-phone{
    background-image: url(../images/icons/phone.svg);
}

.icon-email{
    background-image: url(../images/icons/email.svg);
}

.icon-github {
    background-image: url(../images/icons/github.svg);
}

.home__pic {
    width: 100%;
    height: 100%;
}

@media screen and (max-width:639px) {
    
    .home__pic {
        max-width: 150px;
        max-height: 150px;
    }

    .card-picture {
        display: grid;
        justify-content: center;
        margin: 10px 0;
        min-width: 100px;
        min-height: 100px;
    }

    .card-text {
        display: grid;
        row-gap: 6px;
        margin: 10px 0;
        border-bottom:2px solid black;
        .card-text__header{
            padding-left: 6px;
        }
        .card-text__text{
            padding-left: 10px;
        }
    }

    .icon {
        width: 12px;
        height: 12px;
    }
}

@media screen and (min-width:640px) {
    
    .home__pic {
        max-height: 500px;
        max-width: 400px;
    }
    
    .card-picture {
        display: grid;
        justify-content: center;
        margin: 10px 0;
    }
    .icon {
        width: 16px;
        height: 16px;
    }
}